import Detail from "../components/Detail.vue"
import MovieList from "../components/MovieList.vue"

const routers = [
    {
        path: "/movie-list",
        name: "movie-list",
        component: MovieList
    },
    {
        path: "/movie-detail/:id",
        name: "movie-detail",
        component: Detail
    },
    {
        path: "/",
        redirect: "movie-list"
    },
]
export default routers



